#management .member_list {
    width: 400px;
    margin: 20px;

    display: flex;
    flex-wrap: wrap;
}

#management .member_list .member {
    width: 103px; height: 158px;
    margin: 10px;

    border-radius: 10px;
    background: #D9D9D9;

    overflow: hidden;
    display: flex;
    flex-direction: column;
}
#management .member_list img {
    width: 103px; height: 103px;
}
#management .member_list .nameTag {
    width: 103px; height: 55px;
    background: #F3F3F3;
}
#management .member_list .nameTag .member_name {
    color: #4C88FF;

    font-weight: 600;
    font-size: 13px;
    line-height: 18px;

    margin: 5px 10px;
}
#management .member_list .nameTag .member_position {
    color: #818181;

    font-weight: 500;
    font-size: 12px;
    line-height: 16px;

    margin: 3px 10px;
}

#management .member_list .member:hover {
    scale: 1.05;
}
#management .member_list .member:active {
    scale: 1;
}