.Post {
    width: 915px;
}
.Post iconify-icon {
    font-size: 20em;
}

.Post .top {
    width: 100%;
    height: 50px;

    background: #BF4A4A;
    border-radius: 10px;

    margin-top: 50px;
    margin-bottom: 10px;

    color: white;

    display: flex;
    align-items: center;
}
.Post .top iconify-icon {
    /* font-size: 25px; */
    margin-left: 20px;
    margin-right: 10px;
    color: white;
}
.Post .top .text {
    height: 35px;
    font-size: 20em;
    font-weight: 600;
}

.Post .postView {
    width: 100%;

    border: 1px solid #7E7E7E;
    border-radius: 10px;

    display: flex;
    flex-wrap: wrap;
}
.Post .postView > div {
    margin-left: 30px;
}
.Post .postView .title {
    width: 800px;
    font-size: 20em;
    font-weight: 600;
    line-height: 27px;

    margin-top: 25px;
    margin-bottom: 20px;
}
.Post .postView .option iconify-icon {
    color: #7E7E7E;
    margin-top: 30px;
}
.Post .postView .option .option_list {
    display: none;
    position: absolute;
    width: 72px; height: 85px;
    transform: translate(-40%, 0);

    border: 1px solid #D9D9D9;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.Post .postView .option .option_list div {
    width: 100%;
    height: 28.34px;

    font-size: 14em;
    line-height: 28.333px;
    text-align: center;
}
.Post .postView .option .option_list div:hover{
    background: #D9D9D9;
}

.Post .postView .writer {
    display: flex;
    width: 650px;
}
.Post .postView .writer .profile {
    width: 55px; height: 55px;
    background: #D9D9D9;
}
.Post .postView .writer .info {
    font-size: 16em;
    margin: 5px;
    margin-left: 7px;
}
.Post .postView .writer .info .date {
    color: #7E7E7E;
}
.Post .postView .scope, .Post .postView .view {
    font-size: 16em;
    color: #7E7E7E;

    height: 16px;
    margin-left: 39px;
    margin-top: 25px;
}
.Post .postView .content {
    width: 800px;
    height: 300px;
    margin-top: 50px;

    font-size: 16em;
    line-height: 21.79px;
}
.Post .postView .attachmant {
    margin-bottom: 15px;
}
.Post .postView .attachmant iconify-icon {
    font-size: 20px;
    margin-right: 3px;
    transform: translate(0, 2.5px);
    margin-left: 5px;
}
.Post .postView .attachmant div{
    font-size: 14em;
    line-height: 26px;
}
.Post .postView .attachmant .title{
    font-size: 16em;
    line-height: 18px;
    margin: 0;
    margin-left: -5px;
    margin-bottom: 5px;
}
.Post .commentView .commentTitle {
    font-size: 20em;

    margin-top: 35px;
    margin-left: 10px;
}
.Post .commentView .commentTitle iconify-icon {
    font-size: 20px;
    margin-right: 5px;
    transform: translate(0, 2.5px);
}
.Post .commentView hr {
    margin-top: 10px;
    margin-bottom: 10px;
}

.Post .commentView .commentInput {
    border: 1px solid #B1B1B1;
    border-radius: 10px;

    padding: 20px;

    display: flex;
    flex-wrap: wrap;
}
.Post .commentView .commentInput .writer {
    width: 875px;
    font-weight: 600;
    font-size: 16em;
    line-height: 22px;
}
.Post .commentView .commentInput #content {
    width: 848px;
    height: 80px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;

    margin-top: 10px;
    border: none;
}

.Post .commentView .commentInput .attachmant {
    width: 820px;
}
.Post .commentView .commentInput .attachmant iconify-icon {
    font-size: 20px;
    margin-right: 3px;
    transform: translate(0, 2.5px);
    margin-left: 5px;
}
.Post .commentView .commentInput .attachmant div{
    font-size: 14em;
    line-height: 26px;
    color: #7E7E7E;
}
.Post .commentView .commentInput .attachmant .title {
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: black;
}
.Post .commentView .commentInput .attachmant .title iconify-icon {
    font-size: 20px;
    margin-right: 5px;
    transform: translate(0, 5px);
}
.Post .commentView .commentInput .submit {
    width: 50px; height: 30px;

    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #B1B1B1;
    margin-top: 15px;

    background: none;
    border: none;
}

.Post .commentView .comment {
    width: 100%;

    display: flex;
    flex-wrap: wrap;
    padding: 20px;
}
.Post .commentView .comment > div {
    width: 850px;
    margin-left: 10px;

    display: flex;
    flex-wrap: wrap;
}
.Post .commentView .comment .profile {
    width: 55px; height: 55px;
    background: #D9D9D9;
    margin: 0;
}
.Post .commentView .comment .name {
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    margin-right: 10px;
}
.Post .commentView .comment .date {
    width: 700px;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: #7E7E7E;
}
.Post .commentView .comment .content {
    width: 800px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    margin-top: 10px;
    margin-bottom: 30px;
}
.Post .commentView .comment .attachment {
    font-size: 14em;
    color:#7E7E7E;
}
.Post .commentView .comment .attachment b {
    color: black;
    margin-right: 10px;
}
.Post .commentView .comment hr {
    width: 100%;
    margin-left: -20px;
    margin-top: 20px;
}

